html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #000;
}

p {
  font-size: 1.5rem;
  letter-spacing: 1px;
  line-height: 20px;
  font-weight: 300;
  color: #fff;
}

a {
  color: #fff;
  text-decoration: none;
}

li {
  list-style: none;
}

h1 {
  font-size: 4rem;
  color: #fff;
  text-transform: uppercase;
}

h2 {
  font-size: 3.6rem;
  color: #fff;
  text-transform: uppercase;
}

h3 {
  font-size: 3rem;
  color: #fff;
  text-transform: uppercase;
}

button {
  color: #fff;
  text-transform: uppercase;
}

input::placeholder {
  color: #fff;
  text-transform: uppercase;
}

.contenedor {
  width: 90%;
}

.heading {
  text-transform: uppercase;
  background-color: #000;
}

.header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-top: 2rem;
  padding-bottom: 2rem;
  padding-right: 5rem;
  padding-left: 5rem;
  z-index: 1000;
}
.header h2 {
  cursor: pointer;
  transition: opacity 0.2s;
}
.header h2:hover {
  opacity: 0.7;
}

.header__list {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.header__list li {
  transition: opacity 0.2s;
}
.header__list li:hover {
  opacity: 0.7;
}

.list__link {
  text-transform: uppercase;
}

.cover-presentation {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  gap: 20rem;
  width: 100%;
}
.cover-presentation video {
  width: 100vw;
}

.cover-presentation__contenido {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40rem;
  z-index: 100;
}
.cover-presentation__contenido div p {
  text-align: center;
}

h1 {
  text-align: center;
}

.cover-presentation__formulario {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}

.input-service {
  background-color: #000;
  opacity: 0.5;
  border: 1px #fff solid;
  width: 40rem;
  height: 5rem;
  padding: 3rem;
  border-radius: 5px;
  color: #fff;
}

main {
  width: 100%;
}

.contenedor {
  height: 800px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-items: center;
  width: 100%;
}

.content {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 4rem;
  padding: 7rem;
}

.content__title {
  text-align: left;
}

.content__pharagraft {
  text-align: left;
}

picture {
  width: 50%;
}
picture img {
  width: 100%;
  height: 100%;
}

.btn-white {
  background-color: #fff;
  color: #000;
  width: 30rem;
  height: 5rem;
  padding: auto;
  border-radius: 5px;
  border: 2px solid #000;
  cursor: pointer;
  transition: all 0.3s;
}
.btn-white a {
  color: #000;
}

.btn-white:hover {
  background-color: #000;
  color: #fff;
  width: 30rem;
  height: 5rem;
  padding: auto;
  border-radius: 5px;
  border: 2px solid #fff;
  cursor: pointer;
}
.btn-white:hover a {
  color: #fff;
}

.btn-black {
  background-color: #000;
  color: #fff;
  width: 30rem;
  height: 5rem;
  padding: auto;
  border-radius: 5px;
  border: 2px solid #fff;
  cursor: pointer;
  transition: all 0.3s;
}
.btn-black a {
  color: #fff;
}

.btn-black:hover {
  background-color: #fff;
  color: #000;
  width: 30rem;
  height: 5rem;
  padding: auto;
  border-radius: 5px;
  border: 2px solid #000;
  cursor: pointer;
}
.btn-black:hover a {
  color: #000;
}

.quotes {
  background-color: rgb(69, 69, 69);
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 7rem;
  padding: 7rem;
}

.card-quotes {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  gap: 7rem;
}
.card-quotes p {
  text-align: center;
  margin-bottom: 3rem;
}

.order {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 7rem;
  padding: 10rem;
}

.line {
  height: 24rem;
  background: #fff;
  width: 0.1rem;
}

.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 3rem;
}
.footer ul {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 3rem;
}

.footer__pharagraft a {
  font-weight: 700;
}